export default {
	props: {
		item: {
			type: Object,
			default () {
				return {}
			}
		},
		types: {
			type: String,
			default: ''
		}
	},
	data() {
		return {

		};
	},
	methods: {
		add() {
			this.$emit('addRecordBox')
		},
		toDetail() {
			let item = this.item
			let params = {
				author: item.author,
				browse_count: item.browse_count,
				create_time: item.create_time,
				thumbs_up_count: item.thumbs_up_count,
				title: item.title,
				_id: item._id

			}
			uni.navigateTo({
				url: "/pages/detail/detail?params=" + JSON.stringify(params)
			})
			// uni.$emit('delivery', this.item)
		}
	}
}
